import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('movement')
export class movement extends Component {

    @property
    speed: number = 0.0001;
    start() {

    }

    update(dt) {
        this.node.setPosition(this.node.position.x+100*dt, this.node.position.y);
    }
}

